
import { computed, defineComponent, onMounted, onBeforeMount, ref } from "vue";
import Register from "@/components/website/Register.vue";
import Login from "@/components/website/Login.vue";
import Section2 from "@/components/website/Section2.vue";
import Section3 from "@/components/website/Section3.vue";
import { useStore } from "vuex";
import ApiService from "@/core/services/ApiService";
import { useRoute } from "vue-router";
import Swal from "sweetalert2/dist/sweetalert2.min.js";
import { Actions } from "@/store/enums/StoreEnums";
import { useI18n } from "vue-i18n/index";

export default defineComponent({
  name: "Website",
  components: {
    Register,
    Login,
    Section2,
    Section3,
  },

  setup() {
    const menu = ref(false);
    const title = ref("");
    const subtitle = ref("");
    const store = useStore();
    const active = computed(() => store.getters.Active);
    const register = computed(() => store.getters.Register);
    const login = computed(() => store.getters.Login);
    const user = computed(() => store.getters.currentUser);
    const i18n = useI18n();
    const language = ref("Es");

    i18n.locale.value = localStorage.getItem("lang")
      ? (localStorage.getItem("lang") as string)
      : "es";

    const setLang = (lang) => {
      localStorage.setItem("lang", lang);
      i18n.locale.value = lang;
      language.value = lang === "es" ? "Es" : "En";
    };

    const setRegister = (value) => {
      store.commit("setRegister", value);
    };

    const setLogin = (value) => {
      store.commit("setLogin", value);
    };

    const logOut = () => {
      store.dispatch(Actions.LOGOUT);
      Swal.fire({
        text: "Se ha cerrado sesión satisfactoriamente",
        icon: "success",
        buttonsStyling: false,
        confirmButtonText: "Aceptar",
        customClass: {
          confirmButton: "btn fw-bold btn-light-primary",
        },
      }).then(function () {
        store.commit("setActive", "sheludes");
      });
    };

    const getUser = async () => {
      const localUser = localStorage.getItem("user");
      if (localUser) {
        ApiService.get("/api/auth/me")
          .then(({ data }) => {
            const temp = data;
            store.commit("setUser", temp);
          })
          .catch(() => {
            store.dispatch(Actions.LOGOUT).then();
          });
      }
    };

    onBeforeMount(() => {
      localStorage.removeItem("lang");
      setLang("es");
    });

    onMounted(() => {
      getUser();

      ApiService.get("/api/configuration").then(({ data }) => {
        const welcome_title = data.find((x) => x.name === "welcome_title");
        title.value = welcome_title.value;
        const welcome_subtitle = data.find(
          (x) => x.name === "welcome_subtitle"
        );
        subtitle.value = welcome_subtitle.value;
      });
    });

    return {
      language,
      user,
      title,
      subtitle,
      menu,
      active,
      register,
      login,
      logOut,
      setRegister,
      setLogin,
      setLang,
    };
  },
});
