<template>
  <div class="container-rate" style="margin-top: 50px">
    <div class="row container-form">
      <div class="col-12">
        <h1>{{ $t("searchrates") }}</h1>
        <h6>{{ $t("searchmiltiplesingle") }}</h6>
      </div>
      <div class="col-12 mt-5">
        <h6 class="section-title">{{ $t("myshipments") }}</h6>
        <div class="section-subtitle">
          {{ $t("completedata") }}
        </div>
      </div>
      <div class="col-12 container-details">
        <h6 class="section-title">{{ $t("routedetails") }}</h6>
      </div>
      <div class="row mt-5">
        <div class="col-md-6 col-xl-3">
          <label class="form-label">{{ $t("placeofreception") }}</label>
          <input
            v-model="pol"
            type="text"
            readonly
            autocomplete="off"
            class="form-control form-control-website"
          />
        </div>
        <div class="col-md-6 col-xl-3">
          <label class="form-label"> {{ $t("pol") }} </label>
          <PortSelect :port_id="pol" @update:port_id="setRatePol($event)" />
        </div>
        <div class="col-md-6 col-xl-3">
          <label class="form-label"> {{ $t("pod") }}</label>
          <PortSelect
            :port_id="pod"
            :pol="false"
            @update:port_id="setRatePod($event)"
          />
        </div>
        <div class="col-md-6 col-xl-3" style="padding: -10px">
          <label class="form-label">{{ $t("placeofdelivery") }}</label>
          <input
            v-model="pod"
            readonly
            type="text"
            autocomplete="off"
            class="form-control form-control-website"
          />
        </div>
      </div>
      <!--      <div class="col-12 container-details">
        <h6 class="section-title">MI CARGA</h6>
      </div>
      <div class="row mt-5">
        <div class="col-md-6 col-xl-3">
          <label class="form-label">Tipo de mercancía</label>
          <input
            v-model="deliver"
            type="text"
            autocomplete="off"
            class="form-control form-control-website"
          />
        </div>
        <div class="col-md-6 col-xl-3">
          <label class="form-label">¿Este envío es peligroso?</label>
          <div class="d-flex align-items-center justify-content-around">
            <div class="d-flex align-items-center mt-3">
              <input id="check-1" class="form-check-input" type="checkbox" />
              <label class="form-label" for="check-1">Si</label>
            </div>
            <div class="d-flex align-items-center mt-3">
              <input id="check-2" class="form-check-input" type="checkbox" />
              <label class="form-label" for="check-2">No</label>
            </div>
          </div>
        </div>
        <div class="col-md-6 col-xl-3">
          <label class="form-label">Volumen de envío</label>
          <input
            v-model="reception"
            type="text"
            autocomplete="off"
            class="form-control form-control-website"
          />
        </div>
        <div class="col-md-6 col-xl-3">
          <label class="form-label">Dimensiones</label>
          <input
            v-model="reception"
            type="text"
            autocomplete="off"
            class="form-control form-control-website"
          />
        </div>
      </div>-->
      <div v-if="kind_containers.length !== 0" class="col-12 container-details">
        <h6 class="section-title">{{ $t("containertype") }}</h6>
      </div>
      <div v-if="kind_containers !== []" class="row mt-5">
        <div
          v-for="(item, index) in kind_containers"
          :key="index"
          class="col-md-6 col-xl-3 mb-4"
        >
          <div class="d-flex align-items-center">
            <input
              class="form-check-input"
              type="checkbox"
              :checked="kind_containers_selected[0] === item.code"
              @click="updateKindContainersSelected(item)"
            />
            <label class="form-label">{{ item.web_description }}</label>
          </div>
        </div>
      </div>
      <div class="col-12 container-details">
        <h6 class="section-title">{{ $t("mydata") }}</h6>
      </div>
      <div class="row mt-5 mb-8">
        <div class="col-md-8">
          <label class="form-label">{{ $t("fullname") }}</label>
          <input
            v-model="name"
            type="text"
            autocomplete="off"
            class="form-control form-control-website"
          />
        </div>
        <div class="col-md-4">
          <label class="form-label">CIF</label>
          <input
            v-model="cif"
            type="text"
            autocomplete="off"
            class="form-control form-control-website"
          />
        </div>
        <div class="col-md-4 mt-5">
          <label class="form-label">{{ $t("address") }}</label>
          <input
            v-model="address"
            type="text"
            autocomplete="off"
            class="form-control form-control-website"
          />
        </div>
        <div class="col-md-4 mt-5">
          <label class="form-label">{{ $t("phonenumber") }}</label>
          <input
            v-model="mobile"
            type="text"
            autocomplete="off"
            class="form-control form-control-website"
          />
        </div>
        <div class="col-md-4 mt-5">
          <label class="form-label">{{ $t("email") }} </label>
          <input
            v-model="email"
            type="text"
            autocomplete="off"
            class="form-control form-control-website"
          />
        </div>
      </div>
      <div class="col-12 d-flex">
        <div class="container-btn ms-auto">
          <a href="#" class="btn-clean-2" @click="clear">{{ $t("clean") }}</a>
          <el-button
            class="btn-continue"
            :loading="loading"
            @click="getQuotations"
            >{{ $t("searchrates") }}</el-button
          >
        </div>
      </div>
    </div>
  </div>
  <div
    v-if="quotations.length !== 0"
    id="rateResponse"
    class="col-12 container-search"
  >
    <div class="col-12 container-info">
      <h6 class="section-title">{{ $t("rates") }}</h6>
      <div class="row header-rate">
        <div class="col-5 text-center">{{ $t("container") }}</div>
        <div class="col-5 text-center">{{ $t("pricebyunit") }}</div>
        <div class="col-2"></div>
      </div>
    </div>
    <div v-for="(quotation, index) in quotations" :key="index">
      <RateResponse :quotation="quotation" />
    </div>
    <div v-for="(data, index) in nodata" :key="index">
      <RateNotFoundResponse :item="data" />
    </div>
  </div>
</template>

<script>
import { computed, defineComponent, onMounted, ref } from "vue";
import ApiService from "@/core/services/ApiService";
import PortSelect from "@/components/website/selects/PortSelect.vue";
import RateResponse from "@/components/website/response/RateResponse.vue";
import RateNotFoundResponse from "@/components/website/response/RateNotFoundResponse.vue";
import { useStore } from "vuex";
import { ElNotification } from "element-plus";

export default defineComponent({
  name: "Rate",
  components: { PortSelect, RateResponse, RateNotFoundResponse },
  setup() {
    const store = useStore();
    const loading = ref(false);
    const deliver = ref("");
    const pol = computed(() => store.getters.PolRate);
    const pod = computed(() => store.getters.PodRate);
    const containerType = computed(() => store.getters.ContainerTypeItem);
    const fromSection = computed(() => store.getters.FromSection);
    const reception = ref("");
    const user = computed(() => store.getters.currentUser);

    const name = ref("");
    const email = ref("");
    const cif = ref("");
    const mobile = ref("");
    const address = ref("");

    const kind_containers = ref([]);
    const kind_containers_selected = ref([]);
    const quotations = ref([]);
    const nodata = ref([]);

    const setRatePol = (item) => {
      store.commit("setRatePol", item);
    };

    const setRatePod = (item) => {
      store.commit("setRatePod", item);
    };

    const getKindContainers = () => {
      ApiService.get("/api/kind_container").then(({ data }) => {
        data.forEach(function (value, index) {
          if (value.web_description !== "" && value.web_description !== null) {
            kind_containers.value.push(value);
          }
        });
      });
    };

    const updateKindContainersSelected = (item) => {
      let addFlag = true;
      kind_containers_selected.value.forEach(function (value, index) {
        if (value === item.code && addFlag) {
          kind_containers_selected.value.splice(index, 1);
          addFlag = false;
        }
      });
      if (addFlag) {
        kind_containers_selected.value.push(item.code);
      }
    };

    const getQuotations = () => {
      if (
        kind_containers_selected.value.length === 0 ||
        !pol.value ||
        !pod.value
      ) {
        let message_error;
        if (kind_containers_selected.value.length === 0) {
          message_error = "Debe seleccionar como mínimo un tipo de contenedor";
        }
        if (!pod.value) {
          message_error = "Debe seleccionar como mínimo un puerto de descarga";
        }
        if (!pol.value) {
          message_error = "Debe seleccionar como mínimo un puerto de carga";
        }
        ElNotification({
          title: "Error",
          message: message_error,
          type: "error",
        });
      } else {
        loading.value = true;
        ApiService.query("/api/lima/quotations", {
          params: {
            qt2polcde: pol.value,
            qt2podcde: pod.value,
            qt2kopcde: kind_containers_selected.value,
          },
        }).then(({ data }) => {
          quotations.value = data.tarif;
          nodata.value = data.not_found;
          loading.value = false;
        });
      }
    };

    const clear = () => {
      kind_containers_selected.value = [];
      kind_containers.value = [];
      getKindContainers();
      store.commit("setPolresetModule");
      deliver.value = "";
      reception.value = "";
      name.value = "";
      email.value = "";
      cif.value = "";
      mobile.value = "";
      address.value = "";
      if (user.value.email) {
        name.value = user.value.name + " " + user.value.lastname;
        email.value = user.value.email;
        mobile.value = user.value.phone;
        address.value = user.value.address;
      }
    };

    onMounted(() => {
      getKindContainers();
      console.log(Object.keys(user).length);
      if (user.value.email) {
        name.value = user.value.name + " " + user.value.lastname;
        email.value = user.value.email;
        mobile.value = user.value.phone;
        address.value = user.value.address;
      } else {
        name.value = "";
        email.value = "";
        mobile.value = "";
        address.value = "";
      }
      if (fromSection.value && containerType.value !== "") {
        loading.value = true;
        let containers = [];
        containers.push(containerType.value);
        kind_containers_selected.value = containers;
        ApiService.query("/api/lima/quotations", {
          params: {
            qt2polcde: pol.value,
            qt2podcde: pod.value,
            qt2kopcde: containers,
          },
        }).then(({ data }) => {
          quotations.value = data.tarif;
          nodata.value = data.not_found;
          store.commit("setFromSection3", false);
          store.commit("setContainerType", "");
          loading.value = false;
          setTimeout(function () {
            window.scrollTo(
              0,
              document.getElementById("rateResponse").offsetTop
            );
          }, 100);
        });
      }
    });

    return {
      user,
      kind_containers,
      kind_containers_selected,
      quotations,
      nodata,
      loading,
      deliver,
      pol,
      pod,
      reception,
      name,
      email,
      cif,
      mobile,
      address,
      setRatePol,
      setRatePod,
      getKindContainers,
      updateKindContainersSelected,
      getQuotations,
      clear,
    };
  },
});
</script>
